.shop-container {
  @include flex-center-horiz;
  flex-wrap: wrap;
  gap: $l-spacing;
  padding: $md-spacing;
  margin-top: $l-spacing;

  .card {
    width: clamp(140px, 40%, 400px);

    img {
      width: 100%;
    }

    p {
      font-size: 0.875rem;
    }

    .name {
      margin-top: $xs-spacing;

      // Adds an ellipsis if the name goes over the second line
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .price {
      margin-top: $s-spacing;
      margin-bottom: $s-spacing;
      font-weight: 600;
    }

    button {
      background-color: black;
      color: white;
      border: none;
      padding: 10px 0px;
      border-radius: 16px;
      width: 100%;
      transition: 300ms ease-in-out;
    }

    button:hover {
      background-color: rgb(59, 59, 59);
    }
  }
}

@media (min-width: 1500px) {
  .shop-container {
    margin-right: 100px;
    margin-left: 100px;

    .card {
      width: 25%;
      p {
        font-size: 1.1rem;
      }
    }
  }
}
